import React, { Component } from 'react'
import propTypes from 'prop-types'
export default class CommentAdd extends Component {

    static propTypes = {
        addComment: propTypes.func.isRequired
    }

    state = {
        username: '',
        content: ''
    }
    
    handleSubmit = () => {
        // 收集数据
        const comment = this.state

        // 异常处理
        if(!comment.username || !comment.content){
            alert('请输入完整信息！')
            return
        }

        // 更新状态
        this.props.addComment(comment)

        // 清楚输入数据
        this.setState({
            username: '',
            content: ''
        })
    }

    handleNameChange = (event) => {
        const username = event.target.value;
        this.setState({username})
    }

    handleContentChange = (event) => {
        const content = event.target.value;
        this.setState({content})
    }

    render() {
        return (
            <div className="col-md-4">
                <form className="form-horizontal">
                    <div className="form-group">
                        <label>用户名</label>
                        <input type="text" className="form-control" placeholder="用户名" value={this.state.username}
                            onChange={this.handleNameChange}
                        />
                    </div>
                    <div className="form-group">
                        <label>评论内容</label>
                        <textarea className="form-control" rows="6" placeholder="评论内容" value={this.state.content}
                            onChange={this.handleContentChange}
                        ></textarea>
                    </div>
                    <div className="form-group">
                        <div className="col-sm-offset-2 col-sm-10">
                            <button type="button" className="btn btn-default pull-right" onClick={this.handleSubmit}>提交</button>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}